@import url(https://fonts.googleapis.com/css?family=Share+Tech+Mono&display=swap);
/* Fonts */

/* Reset */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  font-weight: 400;
  color: #3A3A3A;
  background-color: #FBF9F5;
}

code,
.code {
  font-family: 'Share Tech Mono', 'Cutive Mono', source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  height: 56px;
  line-height: 56px;
  color: #FFF;
  background-color: #FD2B70;
}

.Header-title {
  font-size: 24px;
  letter-spacing: 0.3px;
}

.Header-nav {
  text-align: right;
}

img {
  width: 100%;
}

video {
  width: 100%;
  outline: 0;
}

.Card {
  margin-top: 20px;
  background: #FFF;
  border-radius: 8px;
  box-shadow: 0 4px 28px 0 rgba(58,58,58,0.10);
  overflow: hidden;
}

.Card-header {
  padding: 18px 18px 14px 18px;
}

.Card-footer {
  padding: 10px 18px 18px 18px;
}

.Card-container {
  padding: 0 18px 12px 18px;
}

.Card h2 {
  font-size: 16px;
  font-weight: 600;
  color: #FD2B70;
  letter-spacing: 0.17px;
  text-transform: uppercase;
}

.WebLink {
  display: flex;
  align-items: center;
  justify-content: stretch;
  height: 24px;
  margin: 4px 0;
  text-decoration: none;
  color: #3A3A3A;
  border-bottom: 1px solid transparent;
  -webkit-transition: all ease-in-out .3s;
  transition: all ease-in-out .3s;
}

.WebLink:hover {
  color: #FD2B70;
  border-bottom-color: #FD2B70;
}

.WebLink-icon {
  width: 14px;
  padding-right: 4px;
  text-align: center;
}

.WebLink-title {
  flex-grow: 1;
}

.ErrorCard-sign {
  margin: 0 18px 18px 18px;
  padding: 54px 18px 72px 18px;
  text-align: center;
  color: #777;
  background-color: #FBF9F5;
  border-radius: 8px;
}

.ErrorCard-sign .icon {
  font-size: 56px;
}

.ErrorCard-sign h3 {
  margin-top: 2px;
  font-size: 14px;
}


.Button {
  display: block;
  height: 40px;
  margin: 6px 0;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  text-decoration: none;
  color: #FFF;
  background-color: #FD2B70;
  border: 1px solid #FD2B70;
  border-radius: 8px;
  cursor: pointer;
  -webkit-transition: all ease-in-out .3s;
  transition: all ease-in-out .3s;
}

.Button:hover {
  background-color: #DE0B51;
}

.Button-ghost {
  color: #FD2B70;
  background-color: #FFF;
}

.Button-ghost:hover {
  color: #FFF;
  background-color: #DE0B51;
}

.Button svg {
  vertical-align: text-top;
}

.Button svg:first-child {
  font-size: 12px;
  vertical-align: super;
}


footer {
  height: 56px;
  line-height: 56px;
  text-align: center;
}

.Footer-title {
  color: #777;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App-title {
  margin: 48px 0;
  text-align: center;
}

.App-content {
  flex-grow: 1;
}

